.carousel-alt {
  max-width: 630px;
  position: relative;
  background: url("http://cdn.backgroundhost.com/backgrounds/subtlepatterns/subtle_surface.png");
  background-repeat: repeat;
  margin: 0 auto 1rem;
}
.carousel-alt:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 66.6666666667%;
}
.carousel-alt > .carousel-inner {
  position: absolute;
  top: 0px;
  right: 0;
  bottom: 0;
  left: 0;
}
.carousel-alt .carousel-indicators {
  background-color: rgba(0, 0, 0, 0.6);
  bottom: auto;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
}
.carousel-alt .carousel-indicators li {
  text-indent: -9999px;
  overflow: hidden;
  border: none;
  border-radius: 25px;
  box-sizing: content-box;
  width: 12px;
  height: 12px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.carousel-alt .carousel-indicators li.active {
  border-color: #16a385;
  background-color: #16a385;
}
.carousel-alt.chevron-on .carousel-indicators li.active:before {
  content: "";
  color: #16a385;
  font-size: 2.0736em;
  padding-top: 5px;
  top: 4px;
  left: 4px;
}
@media only screen and (min-width : 640px) {
  .carousel-alt.text-indicators {
    max-width: 900px;
  }
  .carousel-alt.text-indicators:before {
    padding-top: 46.6666666667%;
  }
  .carousel-alt.text-indicators .carousel-inner {
    width: 70%;
  }
  .carousel-alt.text-indicators .carousel-inner > .carousel-item {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .carousel-alt.text-indicators .carousel-inner > .carousel-item:before {
    display: inline-block;
    /* width: 100%; */
    width: 1px;
    margin-left: -1px;
    padding-top: 66.6666666667%;
    float: left;
    content: "";
  }
  .carousel-alt.text-indicators .carousel-inner > .carousel-item a {
    float: left;
  }
  .carousel-alt.text-indicators a[class^=carousel-control].right {
    right: 30%;
  }
  .carousel-alt.text-indicators .carousel-indicators {
    display: flex;
    flex-direction: column;
    height: 100%;
    bottom: 0;
    top: 0;
    right: 0;
    max-width: 300px;
    width: 30%;
    left: auto;
  }
  .carousel-alt.text-indicators .carousel-indicators li {
    width: auto;
    flex: 1 1 20%;
    text-indent: 0;
    border-radius: 0;
    background-color: #dddee0;
    display: block;
    position: relative;
    height: auto;
    padding: 0.5rem 1rem;
    flex-direction: column;
    transition: background-color 0.25s ease-out;
    -moz-transition: background-color 0.25s ease-out;
    -webkit-transition: background-color 0.25s ease-out;
    border: 1px solid #c1c3c7;
    text-align: left;
    font-size: 1.2em;
    margin: 0;
  }
  .carousel-alt.text-indicators .carousel-indicators li:first-child {
    border-top: 0;
  }
  .carousel-alt.text-indicators .carousel-indicators li:last-child {
    border-bottom: 0;
  }
  .carousel-alt.text-indicators .carousel-indicators li.active {
    background-color: #585a61;
    color: white;
    border-right: #16a385 10px solid;
    padding-right: 10px;
  }
}

.carousel-control-prev,
.carousel-control-next {
  min-width: 3rem;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 20%;
  font-size: 1.2em;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
}
@media only screen and (min-width : 640px) {
  .no-controls .carousel-control-prev,
.no-controls .carousel-control-next {
    display: none;
  }
}
@media only screen and (min-width : 640px) {
  .carousel-control-prev,
.carousel-control-next {
    width: 10%;
  }
}
@media only screen and (min-width : 1024px) {
  .carousel-control-prev,
.carousel-control-next {
    width: 5%;
  }
}
.carousel-control-prev:hover, .carousel-control-prev:focus,
.carousel-control-next:hover,
.carousel-control-next:focus {
  outline: 0;
  color: #16a385;
  text-decoration: none;
}
.carousel-control-prev .fa,
.carousel-control-next .fa {
  position: absolute;
  top: 50%;
  margin-top: -10px;
  z-index: 5;
  display: inline-block;
}

.carousel-control-prev .fa {
  left: 50%;
  margin-left: -10px;
}

.carousel-control-next {
  left: auto;
  right: 0;
}
.carousel-control-next .fa {
  right: 50%;
  margin-right: -10px;
}

@media only screen and (min-width : 640px) {
  .carousel-control-prev .fas,
.carousel-control-next .fas {
    width: 1.8em;
    height: 1.8em;
    margin-top: -0.6em;
    font-size: 1.8em;
  }

  .carousel-control-prev .fa {
    margin-left: -0.6em;
  }

  .carousel-control-next .fa {
    margin-right: -0.6em;
  }
}
